import React, { Component } from "react";
import LS from './LS'
import Article from "./Article";

//commentBox('5664562892242944-proj');

export class Detailed extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: "",
      kdata: "",
      switchs1: "false",
      ls: "",
      fillb: false,
      status: "",
      loading:true
    };
    this.someFn = this.someFn.bind(this);
    this.mycall2=this.mycall2.bind(this)
    //this.getdata2 = this.getdata2.bind(this);
    //this.forceUpdate();
  }


  mycall2(value)
  {
    this.setState({status:value})
  }

  someFn() {
    //console.log(this.state.switchs1)
    this.props.callbackFromParent2(this.state.switchs1);
    this.props.callbackFromParent10(this.state.fillb);
  }

  getdata(article_id) {
    fetch("https://arpitnodehw8.azurewebsites.net/create?id=" + article_id)
      .then(res => {
        return res.json();
      })
      .then(kdata => {
        //console.log(kdata)
        this.setState({ kdata: kdata ,loading:false});
      });
  }

  componentDidMount() {
    var url = window.location.search;
    //console.log(url);
    url = url.split("=");
    var status = url.includes("true");
    this.setState({ status: status });
    //console.log(url)
    if (localStorage.getItem("bookmark") !== null) {
      //console.log("aaa")
      var arr = JSON.parse(localStorage.getItem("bookmark"));
      //console.log(arr)
      // console.log(data)
      for (var xx in arr) {
        if (arr[xx].id === url[1]) {
          //console.log("aaaa")
          //console.log(arr[xx].id)
          status = true;
          this.setState({ status: status });
        }
      }
    }
    this.setState({ id: url[1] });
    this.getdata(url[1]);
    this.someFn();
  }

  render() {
    //console.log(this.props.loas)
    if(this.state.loading===false)
    {return (
      <div style={{marginLeft:'1%'}}> 
        <Article
          article={this.state.kdata}
          ls={this.props.loas}
          // callbackFromParent={this.mycall}
          status1={this.state.status}
          cbf={this.mycall2}
        />
      </div>
    );}
    else{
      return(<LS loadsc={this.state.loading} />)
  }
  }
}

export default Detailed;
